let timer;

function App() {
  const [state, setState] = useState(sampleBoardConfig);
  //const [state, setState] = useStickyState(sampleBoardConfig, 'boardConfig');

  useEffect(() => {
    //console.log(document.activeElement === document.body);
    //console.log(document.activeElement);
    //console.log(document.activeElement);
    resetTimer();
  }, [state])

  function resetTimer() {
    clearTimeout(timer);
    timer = setTimeout(() => {
      console.log('e');
    }, 1500);
  }

  //const [workSpree, setWorkSpree] = useState();

  //const [overrideTimer, setOverrideTimer] = useState(false);
  // let overrideTimer = false;
  // let workSpreeTimer;

  // function resetTimer() {
  //   clearTimeout(workSpreeTimer);
  //   workSpreeTimer = setTimeout(() => {
  //     //console.log( );
  //     if(!overrideTimer) {
  //       console.log('work spree over');
  //     }
  //     else {
  //       console.log('overrided timer');
  //       clearTimeout(workSpreeTimer);
  //       overrideTimer = false;
  //     }
  //   }, 2000)
  // }


  // useEffect(() => {
  //   ['keyup', 'keydown', 'scroll', 'mouseup', 'mousedown'].map(e => {
  //     window.addEventListener(e, function(e) {
  //       resetTimer();
  //       //console.log(workSpree);
  //       //clearInterval(workSpree);
  //       //console.log(e);
  //       //setWorkSpree();
  //     })
  //   })
  // }, []);

  const stateAffectors = {

    // add a new list
    addList: (taskParams) => {
      const { content } = taskParams;
      const id = `column-${Object.keys(state.columns).length + 1}`;

      const newState = {
        ...state,
        columns: {
          ...state.columns,
          [id]: {
            id: id,
            title: content,
            taskIds: []
          }
        },

        columnOrder: [
          ...state.columnOrder,
          id
        ]
      }

      //console.log(newState);

      setState(newState);
    },

    // set title of a column
    addTitle: (taskParams) => {
      const { id, content } = taskParams;

      const newState = {
        ...state,
        columns: {
          ...state.columns,
          [id]: {
            ...state.columns[id],
            title: content
          }
        }
      }

      setState(newState);
    },

    //add an item to a column
    addItem: (taskParams) => {

      const { task, column } = taskParams;

      task.id = `task-${Object.keys(state.tasks).length + 1}`;

      const newState = {
        ...state,
        tasks: {
          ...state.tasks,
          [task.id]: {
            id: task.id,
            content: task.content
          }
        },
        columns: {
          ...state.columns,
          [column.id]: {
            ...state.columns[column.id],
            taskIds: [
              ...state.columns[column.id].taskIds,

              task.id
            ]
          }
        }
      }

      setState(newState);
    },

    setItemContent: (taskParams) => {
      //console.log(taskParams);
      const { id, content } = taskParams;
      //console.log(id, content);

      const newState = {
        ...state,
        tasks: {
          ...state.tasks,
          [id]: {
            id: id,
            content: content
          }
        }
      }

      //console.log(newState);

      setState(newState);
    }
  }

  function onDragEnd(result) {

    //console.log('drag end');

    //setOverrideTimer(false);
    //overrideTimer = false;
    //resetTimer();

    const { destination, source, draggableId, type } = result;
    if (!destination) return;
    if (destination.droppableId === source.droppableId && destination.index === source.index) return;

    if (type === 'column') {
      const newColumnOrder = Array.from(state.columnOrder);
      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggableId);

      const newState = {
        ...state,
        columnOrder: newColumnOrder,
      };
      setState(newState);
      return;
    }

    const start = state.columns[source.droppableId];
    const finish = state.columns[destination.droppableId];

    if (start === finish) {
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...finish,
        taskIds: newTaskIds,
      }

      const newState = {
        ...state,
        columns: {
          ...state.columns,
          [newColumn.id]: newColumn,
        }
      }

      setState(newState);
      return;
    }

    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = {
      ...start,
      taskIds: startTaskIds
    };

    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finish,
      taskIds: finishTaskIds
    }

    const newState = {
      ...state,
      columns: {
        ...state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish
      }
    }

    //console.log(newState);
    setState(newState);
  }

  return (
  
    <DragDropContext /*onDragStart={e => overrideTimer = true}*/ onDragEnd={onDragEnd}>
      <Droppable droppableId="all-columns" direction="horizontal" type="column" className="board-container">
        {provided => (
          <div
            {...provided.droppableProps}
            ref={provided.innerRef}
          >
            {state.columnOrder.map((columnId, index) => {
              const column = state.columns[columnId];
              const tasks = column.taskIds.map(taskId => state.tasks[taskId]);



              return <ColumnList
                stateAffectors={stateAffectors}
                key={column.id}
                column={column}
                taskMap={tasks}
                index={index}
              ></ColumnList>
            })}
            {provided.placeholder}
            {
              <ListComposer addList={stateAffectors.addList}></ListComposer>
            }
          </div>
        )}
      </Droppable>
    </DragDropContext>
  )
}

export default App;
