import React from 'react';
import ReactDOM from 'react-dom';
import { Draggable } from 'react-beautiful-dnd';

import { TaskLabelManager } from './TaskLabelManager';
import { DynamicTextarea } from '../DynamicTextarea';


export function Task(props) {
  //console.log(props.setItemContent)
  //console.log(props);
  return (
    <Draggable draggableId={props.task.id} index={props.index}>
      {provided => {
        return (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            className="task"
            tabIndex='0'
            // onMouseUp={e => console.log('e')}
            >
            <DynamicTextarea id={props.task.id} autoSelect='false' title={props.task.content} className='task-input' setContent={props.setItemContent}></DynamicTextarea>  
          </div>
        )
      }}
    </Draggable>
  )
}