import React, { useState, createRef } from 'react';

//import { DynamicTextarea } from './DynamicTextarea';

export function ListComposer(props) {

  const { addList } = props;

  const [isComposerShown, setComposer] = useState(false);
  const input = createRef();


  function updateTextareaHeight(e) {
    input.current.style.height = 'auto';
    input.current.style.height = `${e.target.scrollHeight}px`;
  }

  function updateFocus(e) {
    if ((e.key !== 'Enter' && e.key !== 'Escape') || e.shiftKey) return;
    switch (e.key) {
      case 'Enter':
        e.preventDefault();
        checkBlur();
        break;
      case 'Escape':
        checkBlur(true);
        break;
    }
  }

  function checkBlur(shouldAdd) {
    if (shouldAdd === undefined && input.current.value !== '') return addList({content: input.current.value});
    input.current.blur();
  }


  const AddList = () => {
    return (
      <div className='list-addlist' onMouseUp={e => setComposer(true)}>
        <span className="fa-light fa-plus addcardicon"></span>
        <span>Add another list</span>
      </div>
    )
  }

  const Composer = () => {
    return (
      // <DynamicTextarea id={null}
      // autoSelect='false'
      // title={''}
      // className={'list-input'}
      // setContent={props.addList}
      // callback={e => setComposer(false) }
      // ></DynamicTextarea>

      <React.Fragment>
        <textarea
          ref={input}
          className='list-input'
          autoFocus={true}
          spellCheck="false"
          rows='1'
          onInput={e => updateTextareaHeight(e)}
          onKeyDown={e => updateFocus(e)}
          onBlur={e => {
            setComposer(false);
          }}
        >

        </textarea>
        <div className='composer-control'>
          <button className='composer-addcard'
            onMouseDown={e => e.preventDefault()}
            onMouseUp={e => { e.preventDefault(); checkBlur() }}
            // onMouseUp={e => e.preventDefault()}
          >
            Add List
          </button>
          <span className="composer-addcard-icon"
            onMouseUp={e => { e.preventDefault(); input.current.blur() }}
          >
            <span className="fa-light fa-xmark fa-xl"></span>
          </span>
        </div>
      </React.Fragment>
      
    )
  }

  return (
    <div className='list-container'>
      <div className='list-composer'>
        {
          isComposerShown 
          ? <Composer/>
          : <AddList/>
        }
      </div>
    </div>
  )
}