import React from 'react';

import { DynamicTextarea } from './DynamicTextarea';

export function Title(props) {

  return (
    <div className='list-heading'>
      <DynamicTextarea id={props.columnId} autoSelect='true' title={props.title} className={'list-input'} setContent={props.addTitle}></DynamicTextarea>
      <span className='list-manager'>
        <i className="fa-regular fa-ellipsis"></i>
      </span>
    </div>
  )
}