import React, { createRef, useEffect, useState } from 'react';


export function DynamicTextarea(props) {

  //console.log(props);

  const { id, autoSelect, setContent, callback } = props;

  const [textState, setTextState] = useState(false);
  const [DynamicElement, setDynamicElement] = useState('div');

  //const overlay = createRef();
  const dynamic = createRef();
  let Textarea = DynamicElement;

  //const DyanmicElement = textState ? 'textarea' : 'div';


  function blurTitle(e) {
    //console.log(e.shiftKey);
    if ((e.key !== 'Enter' && e.key !== 'Escape') || e.shiftKey) return;
    e.target.blur();
  }
  
  useEffect(() => {
    
    if(textState) {
      calcHeight();
      setDynamicElement('textarea');
      //DyanmicElement = textState ? 'textarea' : 'div';
    }
    else {
      setDynamicElement('div');
    }
  })

  
  function calcHeight(e) {

    dynamic.current.style.height = 'auto';
    dynamic.current.style.height = `${dynamic.current.scrollHeight}px`;

    //overlay.current.style.height = input.current.style.height;
    //overlay.current.style.height = `${e.target.scrollHeight}px`;
  }

  return (

    // <React.Fragment>
      /* <div
        ref={overlay}
        className={`${props.className} overlay`}
        onMouseUp={e => { overlay.current.style.zIndex = 0; input.current.focus(); }}
      >
      </div> */

   
    <Textarea
      autoFocus={true}
      rows='1'
      ref={dynamic}
      className={props.className}
      defaultValue={props.title}
      spellCheck="false"
      onMouseUp={e => { setTextState(true); dynamic.current.focus(); }}
      onFocus={e => { /*e.preventDefault();*/ if (autoSelect === 'true') return e.target.select(); e.target.setSelectionRange(e.target.value.length, e.target.value.length);}}
      onKeyDown={e => blurTitle(e)}
      onBlur={e => { setContent({ id: id, content: dynamic.current.value }); setTextState(false); if(callback !== undefined) callback() }}
      onInput={e => calcHeight(e)}
      // onMouseUp={e => {console.log('mouseup'); e.target.focus()}}
      //onChange={e => }
    >
      { 
        textState
        ? null
        : props.title
      }
    </Textarea>
    // </React.Fragment>
  )
}