import React, { createRef, useEffect, useState } from 'react';


import { ItemList } from './ItemList';
import { Title } from './Title';

import { CardComposer } from './CardComposer';

import { Draggable, Droppable } from 'react-beautiful-dnd';

export function Column(props) {
  
  //const { addItem, columnId } = props;
  //const addItem = props.addItem, columnId = props.column.id;


  const { addItem, setItemContent, addTitle } = props.stateAffectors;
  
  return (
    <Draggable draggableId={props.column.id} index={props.index}>
      {provided => (
        <div
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          ref={provided.innerRef}
          className="list-container"
          tabIndex='-1'
          >
          <Title addTitle={addTitle} title={props.column.title} columnId={props.column.id}></Title>
          <Droppable droppableId={props.column.id} type="task">
            {provided =>
              <React.Fragment>
                <div className={'item-list'}
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                >
                  <ItemList setItemContent={setItemContent} tasks={props.tasks}></ItemList>
                  {provided.placeholder}
                </div>
                <CardComposer addItem={addItem} columnId={props.column.id}></CardComposer>
              </React.Fragment>
            }
          </Droppable>
        </div>
      )}
    </Draggable>
  )
}