import React, { createRef, useEffect, useState } from 'react';
//import reactDom from 'react-dom';

export function CardComposer(props) {

  const { addItem, columnId } = props;

  const [isComposerShown, setComposer] = useState(false);
  // const [isComposerReady, setComposerReady] = useState(false);

  const input = createRef();

  const AddCard = () => {
    return (
      <div className='footer-addcard' onMouseUp={e => { setComposer(true) }}
        tabIndex='0'
      >
        <span className="fa-light fa-plus addcardicon"></span>
        <span>Add a card</span>
      </div>
    )
  }

  function updateTextareaHeight(e) {
    input.current.style.height = 'auto';
    input.current.style.height = `${e.target.scrollHeight}px`;
  }

  function updateFocus(e) {
    if ((e.key !== 'Enter' && e.key !== 'Escape') || e.shiftKey) return;
    switch (e.key) {
      case 'Enter':
        e.preventDefault();
        checkBlur();
        break;
      case 'Escape':
        checkBlur(true);
        break;
    }
  }

  function checkBlur(shouldAdd) {
    //console.log(addItem);
    if (shouldAdd === undefined && input.current.value !== '') return addItem({ task: { content: input.current.value }, column: { id: columnId } });
    //if (input.current.value == '') return;
    input.current.blur();
  }

  const Composer = () => {
    return (
      <div className='composer-container'>
        <textarea
          ref={input}
          className='composer-input'
          autoFocus={true}
          spellCheck="false"
          onInput={e => updateTextareaHeight(e)}
          onKeyDown={e => updateFocus(e)}
          onBlur={e => {
            setComposer(false);
          }}
        >

        </textarea>
        <div className='composer-control'>
          <button className='composer-addcard'
            onMouseDown={e => e.preventDefault()}
            onMouseUp={e => { e.preventDefault(); checkBlur() }}
          >
            Add card
          </button>
          <span className="composer-addcard-icon"
            onMouseUp={e => { e.preventDefault(); input.current.blur() }}
          >
            <span className="fa-light fa-xmark fa-xl"></span>
          </span>
        </div>
      </div>
    )
  }

  return (
    <div className='list-footer'>
      {
        isComposerShown
        ? <Composer></Composer>
        : <AddCard></AddCard>
      }
    </div>
    
  )
}