import React from 'react';

import { Column } from './Column';

export class ColumnList extends React.PureComponent {


  render() {
    const { column, taskMap, index, stateAffectors } = this.props;
    //const tasks = column.taskIds.map(taskId => taskMap[taskId]);
    return <Column stateAffectors={stateAffectors} column={column} tasks={taskMap} index={index} />;
  }
}